export default {
	data() {
		return {
			active: 1,
			phone:"",
			pwd:"",
			nickname:"",
			userInfo:{
				phone:"",
				pwd:""
			}
		}
	},
	onLoad() {

	},
	onShow() {
		uni.getStorage({
		    key:'userInfo',
		    success: (res)=> {
		         this.userInfo=res.data;
				 this.active=2;
		    }
		});
	},
	methods: {
		reg(){
			var dataTemp={
				phone:this.usrname,
				pwd:this.usrpwd,
				nickname:this.usrnickname
			}
			uni.request({




				url: 'http://localhost:3000/user/reg',

				method:'POST',
				data:dataTemp,
			    success: (res) => {
					if(res.data.code==="10001"){
						uni.showModal({
						    title: '提示',
						    content: res.data.msg,
							showCancel:false
						});
					}else {
						uni.showModal({
						    title: '提示',
						    content: res.data.msg,
							showCancel:false,
						    success: ()=>{
								this.active=1;
						    }
						});
					}
			    }
			});
		},
		login(){
			var dataTemp={
				phone:this.usrname,
				pwd:this.usrpwd
			}
			uni.request({




				url: 'http://localhost:3000/user/login',

			    data: dataTemp,
			    method:'POST',
			    success: (res) => {
					if(res.data.code==="10001" || res.data.code==="10002"){
						uni.showModal({
						    title: '提示',
						    content: res.data.msg,
							showCancel:false
						});
					}else {
						uni.showModal({
						    title: '提示',
						    content: res.data.msg,
							showCancel:false,
						    success: ()=>{
								this.active=2;
								this.userInfo=res.data.info;
								// console.log(res.data.info);
								uni.setStorage({
									key:'userInfo',
									data:res.data.info
								})
						    }
						});
					}
			        // console.log(res.data);
			    }
			});
		},
		exit(){
			uni.clearStorage();
			this.active=1;
		}
	}
}
